const commoneUrl  = "http://localhost:8080"; // 
// const commoneUrl  = "http://192.168.1.11:8080"; // 
const userId = uni.getStorageSync("user").id //id

//post
function postRequest(url,data,type){
	var promise = new Promise((resolve,reject) => {
		var postData = data;
		const that = this;
		uni.request({
			url:commoneUrl + url,
			data:postData,
			method:'POST',
			
			header:{
				'content-type': 'application/json',
				'token':uni.getStorageSync('token')//token
			},
				
			success:function(res){
				if(res.statusCode == 500) responseError(res)
				else if(res.statusCode == 200) resolve(res.data);
				
			},
			fail:function(e)
			{
				reject('');
			}
		})
	});
	return promise;
}

//get
function getRequest(url,data,type){
	var promise = new Promise((resolve,reject) => {
		
			var postData = data;
			uni.request({
				url:commoneUrl + url,
				data:postData,
				method:"GET",
				dataType:'json',
				header:{
					'content-type':'application/json',
					'token':uni.getStorageSync('token')
				},
				success:function(res){
					
					if(res.statusCode == 500) responseError(res)
					else if(res.statusCode == 200) resolve(res.data);
				},
				fail:function(e){
					console.log(e)
					reject('');
				}
			});
	});
	return promise;
}
//put
function putRequest(url,data){
	var promise = new Promise((resolve,reject) => {
			var postData = data;
			uni.request({
				url:commoneUrl + url,
				data:postData,
				method:"PUT",
				dataType:'json',
				header:{
					'content-type': 'application/json',
					'token': uni.getStorageSync('token')
				},
				success:function(res){
					if(res.statusCode == 500) responseError(res)
					else if(res.statusCode == 200) resolve(res.data);
				},
				fail:function(e){
					reject('');
				}
			});
	});
	return promise;
}
//del
function delRequest(url,data){
	var promise = new Promise((resolve,reject) => {
			var postData = data;
			uni.request({
				url:commoneUrl + url,
				data:postData,
				method:"DELETE",
				dataType:'json',
				header:{
					'content-type': 'application/json',
					'token': uni.getStorageSync('token')
				},
				success:function(res){
					if(res.statusCode == 500) responseError(res)
					else if(res.statusCode == 200) resolve(res.data);
				},
				fail:function(e){
					reject('');
				}
			});
	});
	return promise;
}
function responseError(res){
	const that =  this;
	switch(res.data.message){
		
		case '401':
		case '':
			uni.showToast({
				
				title: '!',
				duration: 2000,
				icon:'none'
			});
			setTimeout(()=>{
				uni.removeStorageSync('user');
				uni.redirectTo({
					url:'/pages/login/index'
				})
			},1000)
			break;
		default :
			uni.showToast({
				
				title: '!',
				duration: 2000,
				icon:'none'
			});
	}
	
	
	
}

module.exports = {
    post: postRequest,
    get: getRequest,
	put: putRequest,
	del: delRequest,
	ip: commoneUrl,
	userId : userId
};
